<template>
  <div class="app-page home-page">
    <!-- <div
      ref="overlay"
      class="overlay"
    > -->
    <!-- <span
        ref="presentation"
        class="presentation"
      >cartografia da cultura</span> -->
    <!-- </div> -->

    <header class="aside">
      <logo-card inverted />
      <my-menu @callFilter="filterThis($event)" />
    </header>

    <!-- BUTTON LOGIN/PROFILE -->
    <div class="button-area">
      <q-btn
        v-if="!$store.getters['getAuth']"
        flat
        class="btn-custom"
        to="/signIn"
      >
        <span
          class="body-3 bolder"
          to="/singIn"
        >Participar</span>
      </q-btn>

      <q-btn
        v-if="$store.getters['getAuth']"
        flat
        class="btn-custom"
        to="/profile"
      >
        <span
          class="subheading-2 bolder"
          v-if="$store.getters['getAuth']"
          to="/profile"
        >Perfil</span>
      </q-btn>

      <q-btn
        flat
        class="btn-custom"
        @click="$router.push('/documentation')"
      >
        <span
          class="subheading-2 bolder"
          to="/documentation"
        >Documentation</span>
      </q-btn>
    </div>

    <!-- MAP -->
    <main class="map-container">
      <l-map
        style="width: 100%, height: 100%"
        :zoom="zoom"
        :center="[center.lat, center.lng]"
        :options="options"
        :min-zoom="minZoom"
        :max-zoom="maxZoom"
        @update:zoom="zoom = $event"
        @update:center="center = $event"
        @update:bounds="bounds = $event"
      >
        <l-tile-layer
          :url="layers.carto.url"
          :attribution="attribution"
        />

        <l-control-zoom
          v-if="handleResize()"
          position="bottomright"
        />

        <div class="my-markes">
          <l-marker
            v-for="item in markers"
            :key="item.id"
            class="marker-item"
            :lat-lng="item.coordinates"
            @ready="openDefaultMarker($event, item)"
          >
            <l-icon
              class="icon-marker"
              :icon-size="iconSet.iconSize"
              :icon-anchor="iconSet.iconAnchor"
            >
              <img
                :id="`img-icon#${item.id}`"
                :src="item.image"
                class="img-icon"
                :height="iconSet.iconSize[0]"
                :width="iconSet.iconSize[1]"
              >
            </l-icon>

            <l-popup :options="popupOptions">
              <pin-view :pin-view="getPinById(item.id)" />
            </l-popup>
          </l-marker>
        </div>

        <!-- <l-control-attribution position="topleft" prefix="Algo+Ritmo - Research Group" /> -->
      </l-map>
    </main>
    <!--END MAP -->

    <!-- <div class="show" style="position: absolute; top: 150px; left: 40%; z-index: 3;">
      {{ pins }}
    </div> -->
  </div>
</template>

<script>
import {
  LMap,
  LTileLayer,
  LMarker,
  LControlZoom,
  LPopup,
  LIcon,
} from 'vue2-leaflet';
import { mapGetters } from 'vuex';
import { gsap, TweenMax, Expo } from 'gsap';
import { createHelpers } from 'vuex-map-fields';
import PinView from '../components/PinView.vue';
import MyMenu from '../components/Menu.vue';

const { mapFields } = createHelpers({
  getterType: 'maps/getField',
  mutationType: 'maps/updateField',
});

gsap.registerPlugin(TweenMax, Expo);

export default {
  name: 'HomePage',
  title: 'Cartografia da Cultura de Campo Grande',
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LControlZoom,
    LPopup,
    LIcon,
    PinView,
    MyMenu,
  },
  data() {
    return {
      opemNav: false,
      layers: {
        standard: {
          url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
        },
        stadia: {
          name: 'alidade-smooth',
          url: 'https://tiles.stadiamaps.com/tiles/alidade_smooth/{z}/{x}/{y}{r}.png',
        },
        carto: {
          url: 'https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png',
        },
      },
      iconSet: {
        iconSize: [24, 24],
        iconAnchor: [15, -8],
      },
      popupOptions: {
        autoPan: false,
        // keepInView: true,
        // autoPanPaddingTopLeft: [240, 16],
        // closeButton: true,
      },
      filterSelections: [],
    };
  },
  computed: {
    ...mapFields({
      center: 'mapOptions.center',
      bounds: 'mapOptions.bounds',
      attribution: 'mapOptions.attribution',
      zoom: 'zoomSet.zoom',
      minZoom: 'zoomSet.minZoom',
      maxZoom: 'zoomSet.maxZoom',
      options: 'zoomSet.options',
    }),
    ...mapGetters({
      pins: 'pins/loadPinsFiltered',
      markers: 'pins/getMarkers',
    }),
  },
  created() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize();
  },
  mounted() {
    // this.$store.commit('pins/SET_SELECTED_PIN_ID', null);
  },
  unmounted() {
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    openDefaultMarker(mapObject, item) {
      if ((item.id === this.$store.getters['pins/getSelectedPinId'])) {
        mapObject.openPopup();
        this.$store.commit('pins/SET_SELECTED_PIN_ID', null);
      }
    },
    filterThis(el) {
      if (this.filterSelections.includes(el)) {
        // console.log('removeThisFilter', el);
        const index = this.filterSelections.indexOf(el.toString());
        this.filterSelections.splice(index, 1);
      } else {
        // console.log('addThisFilter', el);
        this.filterSelections.push(el.toString());
      }
    },
    opem() {
      this.opemNav = !this.opemNav;
    },
    setCoordinates() {
      this.pins.forEach((item) => this.markers.push({ id: item.id, categoryId: item.categoryId, coordinates: [item.lat, item.long] }));
    },
    getPinById(id) {
      const target = this.pins.find((item) => item.id === id);
      return target;
    },
    homeTransition() {
      const { overlay, presentation } = this.$refs;

      TweenMax.to(presentation, 2, {
        opacity: 0,
        y: -60,
        ease: Expo.easeInOut,
      });

      TweenMax.to(overlay, 2, {
        delay: 1,
        top: '-100%',
        ease: Expo.easeInOut,
      });
    },
    handleResize() {
      const size = window.innerWidth;
      if (size < 1200) {
        return false;
      }
      return true;
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../styles/variables.scss';
@import '../styles/mixins.scss';

* {
  box-sizing: border-box;
}

.home-page {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.overlay {
  z-index: 3;
  position: absolute;
  width: 100%;
  height: 100vh;
  background: #fff;
  top: 0%;
}

.overlay img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.overlay span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  color: black;
  font-size: 30px;
  font-weight: 900;
  letter-spacing: 14px;
  text-transform: uppercase;
}

.aside {
  font-size: 14px;
  left: 16px;
  max-height: calc(100vh - 16px);
  overflow: hidden;
  position: fixed;
  top: 16px;
  width: 180px;
  z-index: 2;

  @include for-tablet-landscape-up {
    width: 200px;
  }

  @include for-big-desktop-up {
    width: 225px;
  }
}

.map-container {
  position: absolute;
  z-index: 0;
  top: 0px;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.marker-item {
  display: none !important;
  z-index: 1;
  height: 50px;
  width: 50px;
  transition: transform 0.5s;
}

.img-icon {
  display: block;

  &:hover {
    transform: scale(1.2);
  }
}

.button-area {
  position: absolute;
  top: 16px;
  right: 16px;
  z-index: 2;
  overflow: hidden;

  @include for-phone-only {
    display: none;
  }
}

.btn-custom {
  box-shadow: none;
  height: 40px;
  min-width: 80px;
  border-radius: 0px;
  background-color: black;
  transition: filter 0.2s;

  &:hover {
    filter: brightness(90%);
  }

  span {
    text-transform: none;
    font-weight: 700;
    color: white;
  }
}

</style>
